	module.exports = {"data":{"markdownRemark":{"html":"<p>Recently the direction of this blog has strayed, and carefully choosing the articles to write carefully, it is in a state where renewal is almost not kept.</p>\n<p>So, I will write out the direction again and think that I will decide. Basically, we will focus on the following 6 categories.</p>\n<ol>\n<li>Of course, because it is the official site of Doujinshi Circle, a story of a self-made coterie work (mainly \"Toho, Bokaro, Original\" game, sometimes an illustration or Ranobet) and its production story is main</li>\n<li>It is a game production connection, technical notes mainly for programming and working environments</li>\n<li>Furthermore, with the game connection, recent game trends and so on (Because the antenna is narrow, it may be less frequent)</li>\n<li>Gadget information for human column and work environment connection (this is also antenna narrow ry)</li>\n<li>Activities related to the shift from the making side to the teaching side which started activities in about six months or so</li>\n<li>I also started from around last year, [Let's study \"work\"] like a story</li>\n<li>After that it is a textual diary, and in some cases my Twitter summary and so on (important)</li>\n</ol>\n<p>Especially in the last few years I realized that absolutely updating will stop if there is not the last one (laugh)<br>\nWriting a textured diary is easy to work because it is blurred in direction, but it is slightly less than the minus of renewal stops, so it is better not to remove this.</p>\n<p>And I <a href=\"/legacy\">hope</a> to quickly move on articles from <a href=\"/legacy\">danmaqlassic</a> . Since <a href=\"/category/unfiled\">40% of the total number of articles has just undergone mechanical migration</a> , it is not in a state where it can be read properly ... ....</p>","frontmatter":{"cover":null,"date":"2016-07-07T21:34:17+09:00","strDate":"2016/7/7","title":"Think about the direction again"}}},"pathContext":{"path":"/en/blog/2016/07/07-vector-of-danmaq/","langKey":"en"}}

